    min-width: fit-content;
    background-image: url("data:image/svg+xml,%3Csvg id='visual' viewBox='0 0 900 600' width='900' height='600' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' version='1.1'%3E%3Cdefs%3E%3Cfilter id='blur1' x='-10%25' y='-10%25' width='120%25' height='120%25'%3E%3CfeFlood flood-opacity='0' result='BackgroundImageFix'%3E%3C/feFlood%3E%3CfeBlend mode='normal' in='SourceGraphic' in2='BackgroundImageFix' result='shape'%3E%3C/feBlend%3E%3CfeGaussianBlur stdDeviation='161' result='effect1_foregroundBlur'%3E%3C/feGaussianBlur%3E%3C/filter%3E%3C/defs%3E%3Crect width='900' height='600' fill='%23a266fc'%3E%3C/rect%3E%3Cg filter='url(%23blur1)'%3E%3Ccircle cx='406' cy='526' fill='%2397ffe5' r='357'%3E%3C/circle%3E%3Ccircle cx='867' cy='581' fill='%23a266fc' r='357'%3E%3C/circle%3E%3Ccircle cx='857' cy='105' fill='%2397ffe5' r='357'%3E%3C/circle%3E%3Ccircle cx='476' cy='353' fill='%2397ffe5' r='357'%3E%3C/circle%3E%3Ccircle cx='211' cy='55' fill='%23a266fc' r='357'%3E%3C/circle%3E%3Ccircle cx='92' cy='511' fill='%2397ffe5' r='357'%3E%3C/circle%3E%3C/g%3E%3C/svg%3E");
background-repeat: no-repeat;
background-size: cover;
}
.input {
    margin: 20px;
    display: flex;
    justify-content: center;
    width: 95%;
    position: relative;
    align-items: center;
  }
  
  .input__box {
    width: 100%;
    border-radius: 50px;
    padding: 20px 30px;
    font-size: 25px;
    border: none;
    transition: 0.2s;
    box-shadow: inset 0 0 5px black;
  }
  
  .input__box:focus {
    box-shadow: 0 0 10px 8000px rgba(0, 0, 0, 0.3);
    outline: none;
  }
  
  .input_submit {
    position: absolute;
    width: 70px;
    height: 50px;
    margin: 12px;
    border-radius: 50px;
    right: 0px;
    border: none;
    font-size: 15px;
    background-color: #2f74c0;
    color: white;
    transition: 0.2s all;
    box-shadow: 0 0 10px black;
  }
  
  .input_submit:hover {
    background-color: #388ae2;
  }
  
  .input_submit:active {
    transform: scale(0.8);
    box-shadow: 0 0 5px black;
  }
  

.cardsContainer{
  display: flex;
    justify-content: center;
    flex-wrap:wrap;
   gap:30px;
  
}

.albumCard {
    height: 270px;
    
    box-shadow: 0 0 5px black;
    background: linear-gradient(#f04b4b, #9198e5);
    flex:0 0 200px;
    max-width: 200px;
    display: flex;
    flex-direction: column;
    opacity: 0.7;
    border-radius: 10px;
    overflow:hidden;
}
.albumCard img{
    border-radius: 10px 70px;
    height: 200px;
    width: 200px;
}
.albumCard:hover{
    transition: 0.3s;
    opacity: 1;
}
.albumCard__h3{
    color: rgb(83, 83, 83);
}
.albumCard__Name--text{
    margin-left: 5px;
    margin-right: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
   
}
a {
  text-decoration: none;
  color:rgb(78, 78, 78);
}

.trackCard {
  cursor:default ;
    height: 270px;
    box-shadow: 0 0 5px black;
    background: linear-gradient(#6020f8, #f1d5f1);
    flex:0 0 200px;
    max-width: 200px;
    display: flex;
    flex-direction: column;
    opacity: 0.6;
    border-radius: 10px;
    overflow:hidden;
}
.trackCard img{
    border-radius: 70px 10px;
    height: 200px;
    width: 200px;
}
.trackCard:hover{
    transition: 0.3s;
    opacity: 0.9;
}
.trackCard__timeandplay{

    display: flex;
    flex-direction: column;
    justify-content:start;
    align-items:center;
    white-space: nowrap;
   
}
.trackCard__timeandplay h3{
width: 160px;
}

@keyframes onHoverSlideRight {
    from {
      transform: translateX(0);
    }to {
        transform: translateX(-10rem);
    }}
    .onHoverSlideRight:hover h3{
        animation:onHoverSlideRight 2s ;
      }


.trackCard__timeandplay h6{
    margin-left: 4px;
    margin-top: 2px;   
}
.play_button {
  display: flex;
  justify-content: center;
  align-items: center;
    padding-top:0.1em;
    padding-left:0.2em ;
    width: 30px;
    height: 30px;
    margin-top: 10px;
    border-radius: 70px;
    border: none;
    background-color: #6020f8;
    color: white;
    transition: 0.2s all;
    box-shadow: 0 0 7px black;
  }
  .play_button:hover {
    background-color: #8b64e6;
  }
  .play_button:active{
    transform: scale(0.9);
    box-shadow: 0 0 2px black;
  }
 